@keyframes expand {
	from {
		width: 0;
	}

	to {
		width: 100%;
	}
}

.image-wrap {
	width: 0; 
	height: 100%;
	overflow: hidden;
	position: relative;
	will-change: transform;
	animation: expand 1s 2.4s cubic-bezier(.5,.04,.12,.99) forwards;
	transition: all 0.3s cubic-bezier(.5,.04,.12,.99);
	filter:brightness(80%);
}

.image-wrap:hover {
	filter: brightness(100%);
	transform: scale(1.03);
}

.image {
	position: absolute;
	top: -10vw;
	width: 80vw;
}
