@keyframes translate-show2 {
	from {
		transform: translateY(100%);
	}

	to {
		transform: translateY(0%);
	}
}

.text {
	color: #EEE5E9;
	font-family: "Technique Sans";
	font-size: 4vw;
	width: 90%;
	margin: 0 auto;
	margin-top: 10vh;
	word-wrap: wrap;
}

.text__word {
	display: inline-block;
	white-space: nowrap;
	overflow: hidden;
	height: 100%;
}

.text__word-content {
	will-change: transform;
	transform: translateY(100%);
	animation: translate-show2 1s 1.5s cubic-bezier(.57,.04,.29,.95) forwards;
}
