@keyframes translate-show {
	from {
		transform: translateY(120%);
	}

	to {
		transform: translateY(20%);
	}
}

.title {
	font-family: "Technique Sans";
	font-size: 8vw;
	font-weight: lighter;
	text-transform: uppercase;
	text-align: center;
	color: #EEE5E9;
	margin: 8vh auto;
	width: 80%;
	line-height: 7vw;
}

.title__word {
	display: inline-block;
	white-space: nowrap;
	overflow: hidden;
}

.title__word-content {
	transform: translateY(120%);
	will-change: transform;
	animation: translate-show 1s 1.5s cubic-bezier(.57,.04,.29,.95) forwards;
}
