@font-face {
	font-family: "Technique Sans";
	src: url("/technique-sans.otf");
}

* {margin: 0; padding: 0;}

body {
	background-color: #1B2021;
}

#root {
	overflow: hidden;
	width: 100vw;
	height: 100vh;
}

#scroll-area {
	width: 100%;
	height: 100%;
	overflow: auto;
	position: relative;
	display: flex;
}

#content {
	width: 100%;
	height: 100%;
	position: sticky;
	top: 0;
}

#content-height {
	height: 500vh; 
	width: 0;
	visibility: hidden;
}

main {
	padding-top: 1px;
	position: relative;
	width: 100%;
	height: 100%;
}

.background {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	display: flex;
	z-index: -1;
}

.background__tile {
	height: 100%;
	flex-grow: 1;
}

.background__tile:not(:last-child) {
	border-right: 1px solid #272E30;
}

.section__cover-img {
	width: 80%;
	height: 50vh;
	margin: 0 auto;
	filter: sepia(0%) brightness(80%);
	opacity: 0.8;
}


